import React, { Component } from "react";
import { Row, Card, CardTitle, Form, Label, Input, Button } from "reactstrap";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { registerUser } from "../../redux/actions";


import IntlMessages from "../../helpers/IntlMessages";
import { Colxx } from "../../components/common/CustomBootstrap";

class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user:{
        email: "o.saidi@gmail.com",
        password: "omar15",
      },
      name: "Omar Saidi"
    };
    this.onPasswordChange = this.onPasswordChange.bind(this);
    this.onEmailChange = this.onEmailChange.bind(this);
    // this.pathHandleChange = this.pathHandleChange.bind(this);

  }

  let 
  onUserRegister = () => {
    if (this.state.user.email !== "" && this.state.user.password !== "") {
      this.props.registerUser(this.state.user, this.props.history)
    }
  }
  

  onEmailChange (event) {
    
    // console.log("Register -> onEmailChange -> event.target.value", event.target.value)
    let val = event.target.value;
    
    this.setState(prevState => ({
      ...prevState,
      user:{
        ...prevState.user,
        email: val
      }
    }))
    
  }

  onPasswordChange(ev) {
    let val = ev.target.value;

    this.setState(prevState => ({
      ...prevState,
      user:{
        ...prevState.user,
        password: val
      }
    }))
  }
  render() {
    return (
      <Row className="h-100">
        <Colxx xxs="12" md="10" className="mx-auto my-auto">
          <Card className="auth-card">
            <div className="position-relative image-side ">
              <p className="text-white h2">DIGIMEDICAL VR Platform</p>
              <p className="white mb-0">
                Please use this form to register your client. <br />
                
              </p>
            </div>
            <div className="form-side">
              <NavLink to={`/`} className="white">
                <span className="logo-single" />
              </NavLink>
              <CardTitle className="mb-4">
                <IntlMessages id="user.register" />
              </CardTitle>
              <Form>
                <Label className="form-group has-float-label mb-4">
                  <Input type="name" defaultValue={this.state.name} />
                  <IntlMessages id="user.fullname" />
                </Label>
                <Label className="form-group has-float-label mb-4">
                  <Input type="email" defaultValue={this.state.user.email} onChange={this.onEmailChange} />
                  <IntlMessages id="user.email" />
                </Label>
                <Label className="form-group has-float-label mb-4">
                  <Input type="password" onChange={this.onPasswordChange}/>
                  <IntlMessages
                    id="user.password"
                    // defaultValue={this.state.user.password}
                  />
                </Label>
                <div className="d-flex justify-content-end align-items-center">
                  <Button
                    color="primary"
                    className="btn-shadow"
                    size="lg"
                    onClick={this.onUserRegister}
                  >
                    <IntlMessages id="user.register-button" />
                  </Button>
                </div>
              </Form>
            </div>
          </Card>
        </Colxx>
      </Row>
    );
  }
}
const mapStateToProps = ({ authUser }) => {
  const { user, loading } = authUser;
  return { user, loading };
};



export default connect(
  mapStateToProps,
  {
    registerUser
  }
)(Register);
